@import "../settings";

@font-face {
    font-family: 'icomoon';
    src:url('fonts/icons/wasentha.eot?mcl5xn');
    src:url('fonts/icons/wasentha.eot?#iefixmcl5xn') format('embedded-opentype'),
        url('fonts/icons/wasentha.woff?mcl5xn') format('woff'),
        url('fonts/icons/wasentha.ttf?mcl5xn') format('truetype'),
        url('fonts/icons/wasentha.svg?mcl5xn#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
}

@mixin icon {
    font-family: 'icomoon', sans-serif;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin stacked-icon {
    display: inline-block;
    vertical-align: top;
    white-space: nowrap;
    position: relative;
    height: 1em;
    width: 1em;
}

@mixin stacked-icon-before-after {
    display: block;
    position: absolute;
    white-space: normal;
    top: 0.143em;
    left: 0.143em;
    text-indent: 0;
    speak: none;
}

[class^='icon-'], [class*=' icon-'], [class^="st-icon-"], [class*=' st-icon-'] {
    @include icon;
}

[class^="st-icon-"], [class*=' st-icon-'] {
    @include stacked-icon;
}

[class^="st-icon-"]:before, [class*=' st-icon-']:before, [class^="st-icon-"]:after, [class*=' st-icon-']:after {
    @include stacked-icon-before-after;
}

